import React from 'react';
import { Component } from 'react';
import  StripeCheckout from 'react-stripe-checkout';
// import Product from '../../../server/models/Product_get_Api/Product';
import axios from 'axios';
// import {toast} from 'react-toastify';

// import "react-toastify/dist/ReactToastify.css";
// import "./styles.css";

// toast.configure();


class placebooking extends Component{
    constructor(){
        super()
        this.state={
            johnJacab:sessionStorage.getItem('johnJacab'),
            product:'',
            firstname:'',
            lastname:'',
            address1:'',
            address2:'',
            city:'',
            state:'',
            pin:'',
            contact:'',
            

        }
        this.changeProductName=this.changeProductName.bind(this)
        this.changeFirstName=this.changeFirstName.bind(this)
        this.changeLastname=this.changeLastname.bind(this)
        this.changeAddress1=this.changeAddress1.bind(this)
        this.changeAddress2=this.changeAddress2.bind(this)
        this.changeCity=this.changeCity.bind(this)
        this.changeState=this.changeState.bind(this)
        this.changePin=this.changePin.bind(this)
        this.changeContact=this.changeContact.bind(this)
        this.onSubmit=this.onSubmit.bind(this)
    }

   
    // handleToken=(token)=>{
    //     console.log({token})
    //     axios.post('http://localhost:4000/app/checkout',{
    //         token,
    //         // product
    //     });
        // const {status}=response.data
        // if(status === 'success'){
        //     toast('Success! Please check your email for details',
        //     {type:'success'})
        // }else{
        //     toast("Something went wrong",{
        //         type:"error"});
        // }
        
    // }
    changeProductName=(event)=>{
        this.setState({
            product:event.target.value
        })
    }
    changeFirstName=(event)=>{
        this.setState({
            firstname:event.target.value
        })
    }
    changeLastname=(event)=>{
        this.setState({
            lastname:event.target.value
        })
    }
    
    changeAddress1=(event)=>{
        this.setState({
            address1:event.target.value
        })
    }

    changeAddress2=(event)=>{
        this.setState({
           address2:event.target.value 
        })
    }

    changeCity=(event)=>{
        this.setState({
            city:event.target.value
        })
    }
    changeState=(event)=>{
        this.setState({
            state:event.target.value
        })
    }
    changePin=(event)=>{
        this.setState({
            pin:event.target.value
        })
    }
    changeContact=(event)=>{
        this.setState({
            contact:event.target.value
        })
    }

    onSubmit=(event)=>{
        event.preventDefault()
        const ordered={
            product:this.state.product,
            firstname:this.state.firstname,
            lastname:this.state.lastname,
            address1:this.state.address1,
            address2:this.state.address2,
            city:this.state.city,
            state:this.state.state,
            pin:this.state.pin,
            contact:this.state.contact
        }
        axios.post('http://localhost:4000/app/detail',ordered)
            .then(res=>console.log(res.data))
            this.setState({
                product:"Full-Rimmed-Frames",
                firstname:'',
                lastname:'',
                address1:'',
                address2:'',
                city:'',
                state:'',
                pin:'',
                contact:''
                })
    }

    render(){
        console.log(this.state.johnJacab,'====>johnJacab')
        return (
            <div className="container" style={{marginBottom:"10px",width:'35%'}}>
                <div className="panel panel-primary">
                <center>
                    <div className="panel-heading" style={{marginRight:'50px'}}>
                        <center><h2>Checkout</h2> </center>
                    </div>
                    </center>
                   
                        {/* <div className="form-group">
                            <label className="control-label">Order_Id:</label>
                            <input type="text" name="order_id" value={this.state.order_id} readOnly className="form-control"/>

                        </div> */}
                            <div className="form-group">
                                <h3>Shipping address</h3>
                                    <label className="control-label">Product Name :</label>
                                    <input type="text" name="Product Name" placeholder="Full-Rimmed Frames"
                                    onChange={this.changeProductName} value={this.state.product} className="form-control" readOnly/>

                            </div>
                    <form onSubmit={this.onSubmit}>
                            <div className="form-group">
                                <input type="text" name="order_id" 
                                value={this.state.firstname}
                                onChange={this.changeFirstName} className="form-control" placeholder="First Name" required/>

                            </div>

                            <div className="form-group">
                                <input type="text" name="order_id" 
                                value={this.state.lastname} className="form-control"
                                onChange={this.changeLastname} placeholder="Last Name" required/>

                            </div>

                            <div className="form-group">
                                <input type="text" name="order_id" value={this.state.address1} className="form-control"
                                onChange={this.changeAddress1} placeholder="Address Line 1" required/>

                            </div>

                            <div className="form-group">
                                <input type="text" name="order_id" value={this.state.address2} className="form-control"
                                onChange={this.changeAddress2} placeholder="Address Line 2" required/>

                            </div>

                            <div className="form-group">
                                <input type="text" name="order_id" value={this.state.city} className="form-control"
                                onChange={this.changeCity} placeholder="city" required/>

                            </div>

                            <div className="form-group">
                                <input type="text" name="order_id" value={this.state.state} className="form-control"
                                onChange={this.changeState} placeholder="State/Province/Region:" required/>

                            </div>
                            <div className="form-group">
                                <input type="number" name="order_id" 
                                value={this.state.pin} className="form-control" 
                                onChange={this.changePin} placeholder="Pin code" required/>

                            </div>
                            <div className="form-group">
                                <input type="number" name="order_id" value={this.state.contact} className="form-control" 
                                onChange={this.changeContact} placeholder="Contact Number" required/>

                            </div>
                            <button type="submit" value="submit" style={{backgroundColor:'#5AB5E9'}}>
    
                            <StripeCheckout
                                stripeKey="pk_test_51IZVXhSA4uKIG9B8jALVhzQdIvNwE5tK6nQzymRZtNFYkEwVrbUWQ6s63sMio4dbhe5yNv0uHwmHUxur7XK37hB0001rRpMBuX"

                                token={this.handleToken}
                                // billingAddress
                                // shippingAddress
                                // amount={Product.price * 100}
                                // name={Product.name}
                            />
                            </button>

                    </form>
                    

                </div>

            {/* <button>PlaceBooking</button> */}

           

            </div>
        )
    }
}

export default placebooking
