import React from 'react';
import {Link} from 'react-router-dom';
import '../Home/TypeLens/Type_Len.css';


const ListingDisplay=(props)=>{
    const renderList=({listData})=>{
        if(listData){
            if(listData.length>0){
                return listData.map((item)=>{
                    console.log(item,"=====?")
                    return(
                        <Link to={`/detail/${item.trip}`}>
                            <div className="differType">
                                <div className="differType_1">
                                        <img src={item.thumb}/>

                                    <div className="differType_2">
                                        {item.city_name}
                                    </div>

                                    <div className="differType_3" style={{marginTop:'5px'}}>
                                            Rs.{item.cost}

                                        </div>


                                    </div>

                                </div>
                            
                    
                        </Link>
                    )
                })
            }else{
                return(
                    <div>
                        <h3>No Data Found</h3>
                    </div>
                )
            }
            }else{ 
                return(
                <div>
                    <img src="/images/loager.gif"/>
                </div>
                )
            }
            
        
    }
    
    return(
        <div>
     
            <br/>
            <br/>
                <center>
                <div style={{backgroundColor:"#E0E0DF",height:"40px", width:"100%",}}>
                       <h3>John Jacab Only Primuim quality frames</h3>
                </div>
                
                {renderList(props)}

            </center>
        </div>
    )

}
export default ListingDisplay;