import React, { Component } from 'react';
import Type_display from './Type_display';
// const Type_of_url="http://localhost:3000/Differ_type_lens";
// const Type_of_url = "http://localhost:4000/app/typoflens";
const Type_of_url = "http://localhost:4000/app/prime";


class TypeOfLens extends Component{
    constructor(){
        super()
         this.state={
            Type_of_url:''
         }
    }
    render(){
        // console.log("Are you comming Api",this.state.Typeurl)
        return(
            <div>
                <h1>OUR RECOMMENDATIONS</h1>
                <h3>Bestsellers</h3>
                <Type_display urlData={this.state.Type_of_url}/>
            </div>
        )
            

        
    }
    componentDidMount(){
        fetch(Type_of_url,{method:"GET"})
        .then((res)=>res.json())
        .then((data)=>{
            console.log('===Type_display==>',data)
            this.setState({Type_of_url:data})
        })
    }
}
export default TypeOfLens;