import React from 'react';
import Carousel from 'react-bootstrap/Carousel';


const ScroolBar=()=>{
  
        return(
            <div style={{backgroundColor: "#fff", height: "auto"}}>
                    <Carousel>
                        <Carousel.Item>
                            <img
                            src="/images/banner_jjb.jpg" alt="scroolbar"
                            style={{ height:"auto", width:"100%" , paddingTop: "480px", marginTop: "-600px", }}/>
                            <Carousel.Caption>
                              
                                <p>NO MIDDLE MEN</p> 
                            </Carousel.Caption>

                        </Carousel.Item>
                        <Carousel.Item>
                            <img 
                            src="/images/banner_jb_2.jpg" alt="banner"
                            style={{ height:"auto", width:"100%" , paddingTop: "480px", marginTop: "-600px", }}/>
                            <Carousel.Caption>
                                
                                <p>NO MARKUPS</p> 
                            </Carousel.Caption>
                        </Carousel.Item>

                        <Carousel.Item>
                            <img
                            src="/images/first_banner.jpg" alt="banner_2"
                            style={{ height:"auto", width:"100%"}}/>
                            <Carousel.Caption>
                                <p>NO MONOPOLY</p> 
                            </Carousel.Caption>
                        </Carousel.Item>

                        <Carousel.Item>
                            <img
                            src="/images/banner_jJ.jpg" alt="banner_2"
                            style={{ height:"auto", width:"100%"}}/>
                            <Carousel.Caption>
                              
                                <p>Primium Lens</p> 
                            </Carousel.Caption>
                        </Carousel.Item>

                    </Carousel>

            </div>
            

        )
}


export default ScroolBar;