import React from 'react';
import './OurStory.css'

const OurStroy=()=>{
    return(
        
        <div style={{marginTop:'100px'}}>
     
            <center>
                <h1>Our Story</h1>
           
            <div style={{backgroundColor:''}}>
            <iframe width="100%" height="700" src="https://www.youtube.com/embed/tgbNymZ7vqY">
            </iframe>
            </div>

        
        </center>
        <br/>
        <br/>
        <div style={{backgroundColor:'#CFEAE3'}}>
        <br/>
        <br/>
        <div className="container">
            <div className="row">
                <div className="col order-last">
                    <h2><span><strong>Fair Prices</strong></span></h2>
                    <p>We have eliminated the middlemen to keep 
                    our prices low, so that you don't have to pay over the  product</p>
                    <br/><br/>
                    <div>
                        <img src="/images/OurStory/our_story.png" alt="Paris" width="300" height="200"/>
                    </div>
                </div>

                <div className="col">
                <h2><span><strong>Design Philosophy</strong></span></h2>
                    <p>Our product team blends
                       centuries-old craftsmanship
                        with modern lifestyles
                      to re-shape optical designs.</p>
                      <br/><br/> 
                    <div>
                        <img src="/images/OurStory/stroy_2.png" alt="Paris" width="300" height="200"/>
                    </div>

                </div>

                <div className="col order-first">
                <h2><span><strong>Finest Quality</strong></span></h2>
                    <p>made of handcrafted
                        Italian acetate, natural pure wood,
                        titanium and bullhorn. So that
                        you get the very best.
                    </p>
                    <br/><br/>
                    <div>
                        <img src="/images/OurStory/out_story_3.png" alt="Paris" width="300" height="200"/>
                    </div>
                </div>

            </div>
            <br/><br/> <br/><br/>
               
        </div>
        </div>

        </div>
    )
}
export default OurStroy;