import React,{Component} from 'react';
import {Link} from 'react-router-dom';
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import Slider from "react-slick";
import 'react-tabs/style/react-tabs.css';
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import axios from 'axios';

const url = "http://localhost:4000/app/detail";
// const url = "http://localhost:3000/Detail_mutlimage_listing";


 var settings = {
      dots: true,
      infinite: true,
      speed: 500,
      slidesToShow: 1,
      slidesToScroll: 1
    };

class HotelDetails extends Component{
    constructor(){
        super()

        this.state={
            johnJacab:'',
            tripid:sessionStorage.getItem('tripid')
        }
    }
    render(){
        // sessionStorage.setItem('johnJacab',this.state.johnJacab.name)
        console.log(this.state.johnJacab,"@===>")
        
        let {johnJacab} = this.state;
        return(
            
            <div className="container">
                <div className="panel panel-primary">
                    <div className="panel-heading">
                        <div className="row">
                            <div className="col-md-12">
                                <h3>{johnJacab.name}</h3>
                                <hr/>                                      
                            </div>
                
                        </div>
                    </div>
                    <div className="panel-body">
                        <div className='row'>
                            <div className="col-md-2"></div>
                            <div className="col-md-8">
                                <Slider {...settings}>
                                    <div>
                                    <img src={johnJacab.thumb} className="img-responsive" style={{width:'80%'}}
                                    />
                                    </div>
                                    <div>
                                    <img src={johnJacab.thumb1} className="img-responsive" style={{width:'80%'}}
                                    />
                                    </div>
                                    <div>
                                    <img src={johnJacab.thumb2} className="img-responsive" style={{width:'80%'}}
                                    />
                                    </div>
                                    
                                </Slider>
                            </div>
                            <div className="col-md-2"></div>
                           
                        </div>
                        <br/>
                        <br/>
                            <center>
                                <h2>Rs.{johnJacab.cost}</h2>
                                <h5>{johnJacab.delivery}</h5>
                            </center>
                            <div>
                                <Tabs>
                                    <TabList>
                                    </TabList>
                            
                                    <TabPanel> 
                                    </TabPanel>
                                </Tabs>
                            </div>
                            <center>
                            <div>
                           
                                <Link to={`/booking/${johnJacab.id}`} className="btn btn-success">Order Now</Link> &nbsp; &nbsp; &nbsp; &nbsp;
                                <Link to={`/list/${this.state.tripid}`} className="btn btn-danger">Back</Link>
                            </div>
                            </center>
                            <br/>
                            <br/>
                    </div>
                </div>
            </div>
            
        )
    }

   async componentDidMount(){
        var john = this.props.match.params.id;
        sessionStorage.setItem('john',john);
        const response= await axios.get(`${url}/${john}`)
            this.setState({johnJacab:response.data[0]})
           
        
    }
}

export default HotelDetails;