import React, { Fragment } from 'react';
import 'bootstrap/dist/css/bootstrap.css';
import {Link} from 'react-router-dom';
// import logo from '../images/logo/logo.png';


const Navbar = () => {
    return (
        <Fragment>
            <nav className="navbar navbar-expand-lg navbar-light bg-light navbar sticky-top">
            <Link className="navbar-brand" to="/">
                <img src='./images/logo/logo.png' alt="john Jacab" style={{height:'45px'}}/>
            </Link>
            
            <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                <span className="navbar-toggler-icon"></span>
            </button>

            <div className="collapse navbar-collapse" id="navbarSupportedContent">
                <ul className="navbar-nav ml-auto">
                    <li className="nav-item active">
                        <Link className="nav-link" to="/">Home <span className="sr-only">(current)</span></Link>
                    </li>
                    <li className="nav-item">
                        <Link className="nav-link" to="/Press">Press</Link>
                    </li>
                    <li className="nav-item">
                        <Link className="nav-link" to="/OurStroy">Our Story</Link>
                    </li>

                    <li className="nav-item">
                        <Link className="nav-link" to="/logedin">Login</Link>
                    </li>

                    <li className="nav-item">
                        <Link className="nav-link" to="/signin">Registration</Link>
                    </li>

                    {/* <li className="nav-item">
                        <Link className="nav-link" to="#">Link</a>
                    </li> */}
          
             
                </ul>
                {/* <form className="form-inline my-2 my-lg-0">
                <input className="form-control mr-sm-2" type="search" placeholder="Search" aria-label="Search"/>
                <button className="btn btn-outline-success my-2 my-sm-0" type="submit">Search</button>
                </form> */}
            </div>
            </nav>
        </Fragment>
    )
}

export default Navbar
