import React, { Component } from 'react';
import './CategoriesCss.css';
import DisplayCategory from './DisplayCategory';
// const url = "http://localhost:3000/categories_Product";
const url = "http://localhost:4000/app/product_category";


class Categories extends Component{
    constructor(){
        super()
            this.state={
                location: '',
            }

    }
    render(){
        // console.log("Hello India",this.state.url)
        return(
            <div className="ccontainer">
                <div>
                    <DisplayCategory urlData={this.state.location}/>
                </div>
            </div>       

        )
    }
   componentDidMount(){
       fetch(url,{method:'GET'})
       .then((res)=>res.json())
       .then((data)=>{
           this.setState({location:data})
       })
   }
}
export default Categories;