import React,{ Component } from 'react';
import {Link} from 'react-router-dom';
import axios from 'axios';
import './signup.css';

class SignUp extends Component{
    constructor(){
        super()
            this.state={
                fullName:'',
                lastname:'',
                email:'',
                password:''
            }

        this.changeFirstName = this.changeFirstName.bind(this)
        this.changeLastname = this.changeLastname.bind(this)
        this.changeEmail = this.changeEmail.bind(this)
        this.changePassword = this.changePassword.bind(this)
        this.onSubmit=this.onSubmit.bind(this)
    }

    changeFirstName=(event)=>{
        this.setState({
            firstname:event.target.value
        })
    }

    changeLastname=(event)=>{
        this.setState({
            lastname:event.target.value
        })
    }

    changeEmail=(event)=>{
        this.setState({
            email:event.target.value
        })
    }

    changePassword=(event)=>{
        this.setState({
            password:event.target.value
        })
    }

    onSubmit=(event)=>{
        event.preventDefault()

        const registered = {
            firstname:this.state.firstname,
            lastname:this.state.lastname,
            email:this.state.email,
            password:this.state.password
        }


        axios.post('http://localhost:4000/app/signup',registered)
                .then(res=>console.log(res.data))

                // this.setState({
                //     firstname:'',
                //     lastname:'',
                //     email:'',
                //     password:''
                // })
                this.props.history.push('/logedin')
    }
    render(){
        return(
            
            <form style={{width:'40%',justifyContent:'center', margin:'auto'}} onSubmit={this.onSubmit}>
            
                <h3>Register</h3>

                <div className="form-group">
                <label>First Name</label>
                <input type="text" className="form-control" placeholder="First name" onChange={this.changeFirstName} value={this.state.firstname} required/>
                </div>

                <div className="form-group">
                    <label>Last name</label>
                    <input type="text" placeholder="Last name" onChange={this.changeLastname} value={this.state.lastname} className="form-control" required/>
                </div>

                <div className="form-group">
                    <label>Email</label>
                    <input type="email" className="form-control" 
                    onChange={this.changeEmail} value={this.state.email}
                    placeholder="Enter Email" required/>
                </div>

                <div className="form-group">
                <label>Password</label>
                <input type="password" className="form-control" onChange={this.changePassword} value={this.state.password}  placeholder="Enter Password" required/>
                </div>

                <button type="submit" className="btn btn-dark btn-block" value="submit">Register</button>
                <p>Already registered <Link to="/logedin">log in?</Link></p>
            </form>
        )
    }
}
export default SignUp;