import React from 'react';
import { Component } from 'react';
import Type_display from './Type_display';
// const typeOfLens=" http://localhost:3000/Differ_type_len";
const typeOfLens = "http://localhost:4000/app/typoflens";

class TypeOfLens extends Component{
    constructor(){
        super()

         this.state={
            typeOfLens:''
         }
    }
    render(){
        return(
            <div>
                <h3>New Arrivals</h3>
                <Type_display  urlData={this.state.typeOfLens}/>
            </div>

        )
    }
    componentDidMount(){
        fetch(typeOfLens,{method:'GET'})
        .then((res)=>res.json())
        .then((data)=>{
            this.setState({typeOfLens:data})
        })
    }
}
export default TypeOfLens