import React from 'react';
import {Link} from 'react-router-dom';

const TwoCategoryDisply=(props)=>{
    console.log('Two_display_page==>',props.urlData)
    const renderList=({urlData})=>{
        if(urlData){
            return(
                urlData.map((item)=>{
                    return(
                        <Link to={`/list/${item.id}`}>
                        <div className="tileContainer">
                            <div className="tileComponent11">
                                <img src={item.Prod_image}/>
                            </div>
                      
                        <div className="tileComponent2">
                            <div className="componentHeading">
                                {item.name}
                            </div>
                            <div className="componentSubHeading">
                                <button >{item.name}</button>
                            </div>

                        </div>
                        </div>

                        </Link>
                    )
                })
            )
        }
    }
    return(
        <div>
            {renderList(props)}
        </div>
    )
}
export default TwoCategoryDisply;