import React from 'react';

const Press=()=>{
    return(
        <div className="container" style={{backgroundColor:'#FFFFFF',height:'450px',marginTop:'100px'}}>
        
            <h2><span><strong>Press</strong></span></h2>
            <hr/>
            <h5>
                We’re relatively new on the block but here’s a secret – we love attention. We’ve had some kind folks talk about us in the media and we feel the party has just started. Here’s to making headlines as we slay into  2019.
            </h5>
        </div>
    )
}
export default Press;