import React, { Component } from 'react';
import TwoCategoryDisply from './TwoCategoryDisplay';
import './Two_category.css';
// const Two_cat= "http://localhost:4000/app/primumlen";
const Two_cat= "http://localhost:4000/app/two_men";


class TwoCategory extends Component{
    constructor(){
        super()

        this.state={
            Two_cat:''
        }
    }
    render(){
        return(
            <div className="container">
                <div>
                    <h2>Men || Women || Unisex Lens </h2>
                    <TwoCategoryDisply urlData={this.state.Two_cat}/>
                </div>

            </div>
      
        )
    }
    componentDidMount(){
        fetch(Two_cat,{method:'GET'})
        .then((res)=>res.json())
        .then((data)=>{
            console.log('Two_api',data)
            this.setState({Two_cat:data})
        })
    }
}
export default TwoCategory;
