import React from 'react';

const Press=()=>{
    return(
        <div className="container" style={{backgroundColor:'#FFFFFF',height:'450px',marginBottom:'30px',marginTop:'100px'}}>
        
            <h2><span><strong>Careers</strong></span></h2>
            <hr/>
            <h5>
            ​​At John Jacobs, we're always looking to improve, and nowhere does this apply more than with our hiring strategy.<br/>

            That's why we are constantly searching for the best qualified candidates to join the JJ team. <br/> <br/>
            
            Our commitment  towards nurturing our team has led to  long relationships with staff members who have helped  build our successes and drive the business forward .  In particular,  John Jacobs prides itself on creating a close-knit working environment based on mutual trust and respect with our employees .  That's why we  offer competitive compensation and incentives along with a generous discount for qualifying employees.  <br/> <br/>
            
            The list of both, general and available positions within the company is given below.  Your application should include your resume, as well as a cover letter explaining how you would contribute at John Jacobs. In case you don’t see a profile that fits your abilities, please write to us to share your skills and ideas. <br/>

            Send in your application at support@john-jacobs.com
            </h5>
            <br/>
            <br/>
        </div>
        
    )
}
export default Press;