import React, { Component } from 'react';
import ListingDisplay from './ListingDisplay';
const ListUrl = "http://localhost:4000/app/listing";
// const ListUrl = "http://localhost:3000/Pure_listing";

class ListingApi extends Component{
    constructor(){
        super()
        this.state={
            ListUrl:''
        }
    }
    render(){
        console.log('listingDisplay===>',this.state.ListUrl)
        return(
            <div className='row'> 
                <div className="col-md-12">
                    <ListingDisplay listData={this.state.ListUrl}/>
                </div>
            </div>
        )
    }
    componentDidMount(){
        var tripid = this.props.match.params.id;       
        sessionStorage.setItem('tripid',tripid);
        fetch(ListUrl,{method:'GET'})
        .then((res)=>res.json())
        .then((data)=>{
            this.setState({ListUrl:data})
        })
    }

}
export default ListingApi;