import React,{ Component } from 'react';
import {Link} from 'react-router-dom';
import axios from 'axios';

class Login extends Component{
    constructor(){
        super()
         this.state={
             email:'',
             password:''
         }
    }
    
    onSubmit=(event)=>{
        event.preventDefault();
        const Login = {
            email:this.state.email,
            password:this.state.password
        }


        axios.post('http://localhost:4000/app/login',Login)
                .then(res=>{
                    // console.log(res.data,"====>FE==>")
                    if(res.data.message==="User signin successfully"){
                        this.props.history.push('/')
                    }else{
                        res.return('/signin')
                    }
                })
       

    }
    changeEmail=(event)=>{
        this.setState({
            email:event.target.value
        })
    }

    changePassword=(event)=>{
        this.setState({
            password:event.target.value
        })
    }

    render(){
        return(
            <form style={{width:'40%',justifyContent:'center', margin:'auto'}} onSubmit={this.onSubmit}>
            
                <h3>Log in</h3>

                <div className="form-group">
                <label>Email</label>
                <input type="email" className="form-control" placeholder="Enter email" onChange={this.changeEmail} required/>
                </div>

                <div className="form-group">
                <label>Password</label>
                <input type="password" className="form-control" placeholder="Enter Password" onChange={this.changePassword} required/>
                </div>

                <div className="form-group">
                <div className="custom-control custom-checkbox">
                <input type="checkbox" className="custom-control-input" id="customCheck1"/> 
                <label className="custom-control-label" htmlFor="customCheck1">Remember me</label>
                </div>
            </div>
            <button type="submit" className="btn btn-dark btn-lg btn-block" >Sign in
            </button>
            <p>Sign up <Link to="/signin">Signin?</Link></p>
            {/* <p className="forgot-password text-left">Forgot<a href="#">Password?</a></p> */}
            </form>
        )
    }
}
export default Login;