import React, {Fragment}from 'react';
import './BestDeal.css';

const BestDeal=()=>{
    return(
        <Fragment>
        <hr/>
        <div className="cont">
        <center><h1><b>EXCLUSIVE COLLECTION ON BIG MART STORE</b></h1></center>
        <br/>
        <div className="row">
          
            <div className="col-md-5" style={{display: "block", textAlign: "center"}}>
                <h2><b>Our Big Mark is open 24/7</b></h2>
                <p>Visit your Big Mark store Any time :</p>
                <li>Free delivery with no any extra charges</li>
                <li>Expert guidance of stylists to help you make the right choice</li>
                <li>Fully sanitised and safe shopping</li>
                <br/>
                <button className="btn" style={{borderRadius:"10px"}}> Fully Online Store</button>
            </div>
         

            <div className="col-md-6">
                <img src="/images/bannerImg.jpg" alt="John" style={{ width:"800px"}}/>

            </div>
            <div className="col-md-1">

            </div> 
        </div>
        </div>
        <hr/>
        </Fragment>
    )
}

export default BestDeal;