import React from 'react';
import {Link} from 'react-router-dom';
import './Type_Len.css'

const Type_display=(props)=>{
    // console.log('==>coming from typeOfLens', props.urlData)
    const renderList=({urlData})=>{
        if(urlData.length>0){
        if(urlData){
            return(
                urlData.map((item)=>{
                    return(
                        <Link to={`/list/${item.glassess}`}>
                            <div className="differType">
                                <div className="differType_1"> 
                                        <img src={item.image}/>
                                
                               
                                    
                                    <div className="differType_2">
                                        <p>{item.modal}</p>
                                    </div>

                                    <div className="differType_3">
                                        <p>{item.price}</p>

                                    </div>
                                </div>

                            </div>

                        </Link>
                    )
                })
            )

        }
    }

    }
    return(
        <div>
            {renderList(props)}
        </div>
    )
}
export default Type_display;